import {
	TableCell,
	TableHead,
	TableSortLabel,
	TableRow,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { visuallyHidden } from '@material-ui/utils';
import { SortTableHeadProps, HeadCell } from '../../../../../../types';
import { TPropertyDoc } from '../types';
import { headerCells } from './helpers';

const useStyles = makeStyles(() => ({
	sortSpan: { ...visuallyHidden },
}));

const DocumentListHeader = (props: SortTableHeadProps<TPropertyDoc>) => {
	const { order, orderBy, onRequestSort = () => {} } = props;
	const classes = useStyles();

	const isSorted = (headCell: HeadCell<TPropertyDoc>): undefined | boolean =>
		orderBy && orderBy.id === headCell.id;

	return (
		<TableHead>
			<TableRow>
				{headerCells.map((headCell: HeadCell<TPropertyDoc>, index) => (
					<TableCell
						key={headCell.id}
						align={headCell.numeric ? 'right' : 'left'}
						padding={headCell.disablePadding ? 'none' : 'normal'}
						sortDirection={isSorted(headCell) ? order : undefined}
						sx={{ width: !index ? '20%' : 'auto' }}
					>
						<TableSortLabel
							active={isSorted(headCell)}
							direction={isSorted(headCell) ? order : 'asc'}
							onClick={() => onRequestSort(headCell)}
						>
							{headCell.label}
							{isSorted(headCell) ? (
								<span className={classes.sortSpan}>
									{order === 'desc' ? 'sorted descending' : 'sorted ascending'}
								</span>
							) : null}
						</TableSortLabel>
					</TableCell>
				))}
				<TableCell sortDirection={false} align='center'>
					Action
				</TableCell>
			</TableRow>
		</TableHead>
	);
};

export default DocumentListHeader;
