import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	IconButton,
	Grid,
} from '@material-ui/core';
import {
	EditTwoTone as EditIcon,
	FileDownload as FileDownloadIcon,
} from '@material-ui/icons';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { TPropertyDoc, TMappedSettings, TMappedDocTypes } from '../types';
import Header from './DocumentListHeader';
import { formatDate } from '../../../../../../helpers';
import { headerCells } from './helpers';
import { useOrder } from '../../../../../../hooks';
import {
	getHeadCellById,
	getComparator,
	stableSort,
} from '../../../../../../utils/table';

type Props = {
	docs: Array<TPropertyDoc>;
	handleOpenEditDialog: Function;
	handleDownloadDocs: Function;
	docStatuses: TMappedSettings;
	docPermissions: TMappedSettings;
	docTypes: TMappedDocTypes;
	isDownloading: boolean;
	isLoadingProperty: boolean;
};

const DocumentList = ({
	docs,
	handleOpenEditDialog,
	handleDownloadDocs,
	docStatuses,
	docPermissions,
	docTypes,
	isDownloading,
	isLoadingProperty,
}: Props) => {
	const displayDocPermissions = (currentPermissions: Array<string>) =>
		currentPermissions.reduce(
			(result, permission) =>
				result
					? `${result}, ${docPermissions[permission]}`
					: `${docPermissions[permission]}`,
			''
		);

	const defaultOrder = getHeadCellById('CreatedAt', headerCells);
	const { order, orderBy, handleRequestSort } = useOrder(defaultOrder);

	const sortedDocs = stableSort<TPropertyDoc>(
		docs,
		getComparator(order, orderBy)
	);

	return (
		<>
			<TableContainer>
				<Table aria-labelledby='tableTitle' size='small'>
					<Header
						order={order}
						orderBy={orderBy}
						onRequestSort={handleRequestSort}
					/>{' '}
					<TableBody>
						{sortedDocs.map(doc => (
							<TableRow hover tabIndex={-1} key={doc.ObjectKey}>
								<TableCell align='left' sx={{ width: '20%' }}>
									{_get(doc, ['File', 'name'])}
								</TableCell>
								<TableCell align='left'>
									{_get(docTypes, [doc.DocType, 'name'], '')}
								</TableCell>
								<TableCell align='left'>{docStatuses[doc.DocStatus]}</TableCell>
								<TableCell align='left'>
									{displayDocPermissions(doc.DocPermissions)}
								</TableCell>
								<TableCell align='left'>{doc.OfferNo}</TableCell>
								<TableCell align='left'>{formatDate(doc.EventTime)}</TableCell>
								<TableCell align='center'>
									<IconButton
										color='primary'
										onClick={() => {
											handleOpenEditDialog(doc);
										}}
									>
										<EditIcon sx={{ fontSize: '1.3rem' }} />
									</IconButton>
									<IconButton
										color='primary'
										onClick={() => handleDownloadDocs([doc.ObjectKey])}
										disabled={isDownloading}
									>
										<FileDownloadIcon sx={{ fontSize: '1.3rem' }} />
									</IconButton>
								</TableCell>
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
			{!isLoadingProperty && _isEmpty(docs) && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
		</>
	);
};

export default DocumentList;
